import React from 'react'
import Delete from '@material-ui/icons/Delete'
import { useCart, useDispatchCart } from '../components/ContextReducer';
// import trash from "../trash.svg"


export default function Cart() {
  let data = useCart();
  let dispatch = useDispatchCart();
  if (data.length === 0) {
    return (
      <div>
        <div className='m-5 w-100 text-center fs-3 ' style={{"color":"#fff"}}>The Cart is Empty!</div>
      </div>
    )
  }


  const handleCheckOut = async () => {
    let userEmail = localStorage.getItem("userEmail");
    let response = await fetch("http://localhost:5000/api/orderData", {
   
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
      
        email: userEmail,
        order_data: data,
        order_date: new Date().toDateString()
      })
    });
    console.log("Order RESPONSE:", response)
    if (response.status === 200) {
      dispatch({ type: "DROP" })
    }
  }

  let totalPrice = data.reduce((total, food) => total + food.price, 0)
  return (
    <div>

      {console.log(data)}
      <div className='container m-auto mt-5 table-responsive  table-responsive-sm table-responsive-md'  style={{"color":"#000"}} >
        <table className='table table-hover '  >
          <thead className='  text-success fs-4'>
            <tr >
              <th scope='col' >#</th>
              <th scope='col' >Name</th>
              <th scope='col' >Quantity</th>
              <th scope='col' >Option</th>
              <th scope='col' >Amount</th>
              <th scope='col' ></th>
            </tr>
          </thead>
          <tbody   style={{"color":"#fff"}}>
            {data.map((food, index) => (
              <tr>
                <th scope='row' >{index + 1}</th>
                <td  style={{"color":"#00ffff"}}>{food.name}</td>
                <td  style={{"color":"#00ffff"}}>{food.qty}</td>
                <td  style={{"color":"#00ffff"}}>{food.size}</td>
                <td  style={{"color":"#00ffff"}}>{food.price}</td>
                <td   style={{"color":"#00ffff"}}><button   style={{"color":"#fff"}} type="button" className="btn  p-0"><Delete onClick={() =>{ dispatch({ type: "REMOVE", index: index }) }} /></button> </td></tr>
            ))}
          </tbody>
        </table>
        <div><h1 className='fs-2 '  style={{"color":"#fff"}}>Total Price: {totalPrice}/-</h1></div>
        <div>
          <button className='btn bg-success mt-5 ' onClick={handleCheckOut} > Check Out </button>
        </div>
      </div>



    </div>
  )
}